.bounce {
    -webkit-animation: bounce 0.8s ease infinite;
            animation: bounce 0.8s ease infinite;
    display: inline-block;
}

@-webkit-keyframes bounce {
    0% {
        -webkit-transform: scale(1, 1) translateY(0);
                transform: scale(1, 1) translateY(0);
    }
    10% {
        -webkit-transform: scale(1.1, .9) translateY(0);
                transform: scale(1.1, .9) translateY(0);
    }
    30% {
        -webkit-transform: scale(.9, 1.1) translateY(-10px);
                transform: scale(.9, 1.1) translateY(-10px);
    }
    50% {
        -webkit-transform: scale(1.05, .95) translateY(0);
                transform: scale(1.05, .95) translateY(0);
    }
    57% {
        -webkit-transform: scale(1, 1) translateY(-1px);
                transform: scale(1, 1) translateY(-1px);
    }
    64% {
        -webkit-transform: scale(1, 1) translateY(0);
                transform: scale(1, 1) translateY(0);
    }
    100% {
        -webkit-transform: scale(1, 1) translateY(0);
                transform: scale(1, 1) translateY(0);
    }
}

@keyframes bounce {
    0% {
        -webkit-transform: scale(1, 1) translateY(0);
                transform: scale(1, 1) translateY(0);
    }
    10% {
        -webkit-transform: scale(1.1, .9) translateY(0);
                transform: scale(1.1, .9) translateY(0);
    }
    30% {
        -webkit-transform: scale(.9, 1.1) translateY(-10px);
                transform: scale(.9, 1.1) translateY(-10px);
    }
    50% {
        -webkit-transform: scale(1.05, .95) translateY(0);
                transform: scale(1.05, .95) translateY(0);
    }
    57% {
        -webkit-transform: scale(1, 1) translateY(-1px);
                transform: scale(1, 1) translateY(-1px);
    }
    64% {
        -webkit-transform: scale(1, 1) translateY(0);
                transform: scale(1, 1) translateY(0);
    }
    100% {
        -webkit-transform: scale(1, 1) translateY(0);
                transform: scale(1, 1) translateY(0);
    }
}




